* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html {
	font-size: 62.5%;
}
body {
	background-color: #fff;
	font-family: 'Montserrat', sans-serif;
	color: #000;
}
.nav {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-size: 2rem;
	padding: 1em 2em;
	width: 100%;
	z-index: 100000;
	color: #000;
	&__logo {
		display: flex;
		align-items: center;
		justify-content: start;
		font-size: 2.3rem;
		text-transform: uppercase;
		letter-spacing: 2px;
		font-weight: 700;
		&__icon {
			height: 50px;
			width: 50px;
		}
		p {
			margin-left: 0.5em;
		}
	}
	&__elements {
		display: none;
	}
	&__burger {
		display: block;
		font-size: 2.5rem;
		color: #000;
	}
}

@media (min-width: 996px) {
	.nav {
		&__elements {
			display: flex;
			&__element {
				margin-inline: 0.4em;
				padding: 0.3em;
				text-decoration: none;
				font-weight: 500;
				letter-spacing: 1px;
				color: #000;
			}
		}
		&__burger {
			display: none;
		}
	}
}
