  padding: 1em 0;
  background-color: #fff;
  color: #5000ca;
  text-align: center;
  border: 1px solid #5000ca;
  font-weight: bolder;
  letter-spacing: 1px;
  border-radius: 8px;
  cursor: pointer;
}
.loginSite .whiteSpace__login__btn--active {
  color: #fff;
  background-color: #5000ca;
}
.loginSite .blueSpace {
