.posts {
	display: flex;

	justify-content: center;
	position: relative;
	flex-direction: column;
	max-width: 800px;
	margin: 0 auto;
	padding-top: 12em;
	min-height: 50vh;
	top: 30%;

	.postCreator {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 1em;
		margin-bottom: 3em;
		border: 1px solid black;
		border-radius: 8px;

		* {
			margin-bottom: 1em;
		}
        &__label{
            font-size: 1.7rem;
            font-weight: 600;
        }
        &__textarea{
            font-size: 1.6rem;
            padding: 0.4em 0.6em;
            resize: vertical;
        }
		&__button {
			text-transform: uppercase;
            font-size: 1.6rem;
			padding: 0.5em 1.5em;
			align-self: end;
			background-color: #5000ca;
            color: #fff;
            border: none;
            border-radius: 8px;
		}
	}

	.post {
		padding: 1.5em;
		margin-bottom: 3em;
		border: 1px solid black;
		border-radius: 8px;
		margin-inline: 0.3em;

		&__profile {
			display: flex;
			align-items: center;
			&__data {
				margin-left: 1em;
			}
			&__nickname {
				font-size: 1.5rem;
			}
			&__date {
				font-size: 1.2rem;
			}
			&__image {
				height: 55px;
				width: 55px;
				object-fit: cover;
				border-radius: 50%;
			}
		}
		&__text {
			margin: 1em 0;

			font-size: 1.5rem;
			font-weight: 500;
		}
		&__stats {
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-weight: bold;
			font-size: 1.2rem;
			padding: 0.2em 0.4em;
		}
		&__eventList {
			display: flex;
			align-items: center;
			justify-content: space-around;
			font-size: 1.2rem;
			text-transform: uppercase;
			font-weight: bolder;
			cursor: pointer;
			margin: 0.5em 1em 0 0.5em;

			border-top: 1px solid #3339;
			border-bottom: 1px solid #3339;
			:nth-child(2) {
				i {
					margin-top: 2px;
				}
			}
			:nth-child(3) {
				display: none;
			}

			&__element {
				display: flex;
				justify-content: center;
				padding: 1em 0;
				flex-grow: 1;
				i {
					margin-right: 0.3em;
					font-size: 1.5rem;
				}
				transition: background-color 0.3s;
				&:hover {
					background-color: #ccc;
				}
			}
		}
	}
}

@media (min-width: 768px) {
	.posts {
		.post {
			&__text {
				margin: 1em 0;
				padding: 1em 2em;
				font-size: 1.5rem;
				font-weight: 500;
			}
			&__eventList {
				:nth-child(3) {
					display: block;
				}
			}
		}
	}
}
