// @use '../../../sass/__colors';

.loginSite {
	position: relative;
	width: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	.whiteSpace {
		display: flex;
		align-items: center;
		justify-content: center;
		&__login {
			width: clamp(300px, 50vw, 500px);
			display: flex;
			flex-direction: column;
			padding: 3em 2.5em;
			font-size: 1.8rem;
			border-radius: 8px;
			* {
				margin: 0.5em 0;
			}
			h1 {
				font-size: 3rem;
				margin-bottom: 1em;
			}
			div {
				display: flex;
				flex-direction: column;
				label {
					font-size: 1.6rem;
					font-weight: 500;
				}
			}
			&__input {
				font-size: 1em;
				border-radius: 8px;
				border: none;
				background-color: #ccc;
				padding: 0.6em;
				width: 100%;
			}

			label,
			p {
				font-size: 1.4rem;
			}
			p {
				position: relative;
				text-align: center;
				&::before,
				&::after {
					content: '';
					position: absolute;
					width: clamp(10px, 5vw, 20%);
					height: 2px;
					background-color: #ccc;
				}
				&::before {
					left: 8%;
					top: calc(50% - 2px);
				}
				&::after {
					right: 8%;
					top: calc(50% - 2px);
				}
			}
			&__btn {
				font-size: 1.4rem;
				text-transform: uppercase;
				padding: 1em 0;
				background-color: #fff;
				color: #5000ca;
				text-align: center;
				border: 1px solid #5000ca;
				font-weight: bolder;
				letter-spacing: 1px;
				border-radius: 8px;
				cursor: pointer;
				&--active {
					color: #fff;
					background-color: #5000ca;
				}
			}
		}
	}
	.blueSpace {
		display: none;
	}
}

@media (min-width: 996px) {
	.nav {
		&__elements {
			&__element {
				color: #fff;
			}
		}
	}

	.loginSite {
		display: flex;
		flex-direction: row;
		gap: 0;
		.whiteSpace {
			width: 50vw;
		}
		.blueSpace {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 50vw;
			padding: 2em 4em;
			color: #fff;
			background-image: url('../images/bgCity.jpg');
			background-size: cover;
			background-repeat: no-repeat;
			height: 100%;
			z-index: -1;
			&__shadow {
				position: absolute;
				width: 100%;
				height: 100%;
				background-color: #5000cabb;
				z-index: -1;
			}
			h2 {
				font-size: 3.5rem;
				margin-bottom: 1em;
			}
			p {
				text-align: center;
				font-size: 2rem;
				padding: 0 4em;
			}
		}
	}
}
